.button{
    width: 80%;
    height: 1.7em;
    background-color: rgb(219, 105, 39);
    border: 2px solid black;
    margin: .5em 1em;
    font-weight: 700;
}

.button:hover{
    cursor: pointer;
    background-color: rgb(179, 83, 28);
}

.button:active{
    background-color: rgb(136, 63, 21);
    color: white;
}