.producto{
    border: 1px solid black;
    margin: .8em 0;
    padding: 1em .5em;
    width: 11em;
    height: 15em;
    border-radius: .6em;
    background-color: #5e330281;
    position: relative;
    overflow: hidden;
}

.producto__img{
    width: 100%;
    border-radius: .8em;
    height: 65%;
}

.producto__title{
    font-size: 1em;
    color: white;
    font-weight: 400;
    text-align: center;
}

.producto__details{
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.producto__newPrice{
    font-size: .9em;
    color: rgb(221, 213, 213);
    font-weight: 700;
    letter-spacing: .1em;
}

.producto__oldPrice{
    font-size: .8em;
    color: rgb(221, 213, 213);
    font-weight: 400;
    letter-spacing: .1em;
}

.producto__discount{
    font-size: .6em;
    font-weight: 700;
    background-color: red;
    color: white;
    width: 3em;
    position: absolute;
    top: 0;
    right: 0;
    text-align: center;
    
}

