.contenedor{
    border: 1px solid black;
    background-color: #92500581;
    width: 38em;
    min-height: 26em;
    margin: 2em auto;
    padding: 1em;
    border-radius: .6em;
    display: flex;
    flex-wrap: wrap;
    gap: 2em;
    justify-content: center;
    align-items: center;
}
.contenedor__title{
    font-size: 1em;
    color: white;
    font-weight: 500;
    text-transform: uppercase;
    text-align: center;
    margin-top: 2em;
    
}

@media (min-width: 768px) {
    .contenedor{
        width: 65%;
    }
}